/* slider css //////////////////////////////////////////////////////////////////////////////////////////////*/
.project-links {
    -webkit-appearance: none;
            -moz-appearance: none;
         appearance: none;
    -webkit-filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
            filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
    -webkit-transition: all .5s ease;
           filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
    transition: all .5s ease;
    border: none;
    background:white;
    border-radius: 30px;
    text-transform: uppercase;
    box-sizing: border-box;
    padding: 15px 40px;
    font-weight: 400;
    font-size: 13px;
    cursor: cursor;
    margin-right: 10px;
    color: blueviolet;
}
  
.project-links:hover {
    color: #FFFFFF;
    background: #222222;
    -webkit-filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
    filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
}

.slider-wrapper {
    position: relative;
    margin-top: 3%;
    margin-left: 5%;
    width: 90%;
    height: 70vh;
    overflow: hidden;
    border: 3px solid rgb(165, 3, 187);
    border-radius: 20px;
    box-shadow: 0 0 100px purple;
}
  
.slide {
    height: 70vh;
    background-size: cover !important;
}
  
.slide::before {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
      bottom: 0;
      left: 0;
}
  
.previousButton, .nextButton {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          z-index: 10;
          background: url(/My-Portfolio/static/media/right-arrow.2915ae22.png) no-repeat center center / 100px;
          width: 32px;
          height: 32px;
          text-indent: -9999px;
          cursor: pointer;
}
  
.previousButton:hover, .nextButton:hover {
    background: url(/My-Portfolio/static/media/right-arrow.2915ae22.png) no-repeat center center / 100px;
}
  
.previousButton {
    left: 0;
    transform: rotate(180deg) translateY(calc(50% + 0px));
  }
  
.previousButton:hover {
    left: -10px;
  }
  
.nextButton {
    right: 0;
  }
  
.nextButton:hover {
    right: -10px;
  }
  
.slider-content {
    text-align: center;
  }
  
.slider-content .inner {
    padding: 0 70px;
    box-sizing: border-box;
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
.slider-content .inner button{
    transition-delay: 0s;
  }
  
.slider-content .inner h1 {
    font-weight: 900;
    margin: 0 auto;
    max-width: 840px;
    color: #FFFFFF;
    font-size: 64px;
    line-height: 1;
  }
  
.slider-content .inner p {
    color: #FFFFFF;
    font-size: 14px;
    line-height: 1.5;
    margin: 20px auto 30px;
    max-width: 640px;
  }
  
.slider-content section {
    position: absolute;
    bottom: 20px;
    left: 20px;
  }
  
.slider-content section span {
    color: #FFFFFF;
  }
  
.slider-content section span {
    color: rgba(255, 255, 255, 0.5);
    font-size: 12px;
    display: inline-block;
    text-align: left;
    line-height: 1.4;
    vertical-align: middle;
    margin-left: 10px;
  }
  
.slider-content section img {
    width: 40px;
    height: 40px;
    border: solid 2px rgba(255, 255, 255, 0.5);
    border-radius: 100%;
    vertical-align: middle;
  }
  
.slider-content section span strong {
    color: #FFFFFF;
    font-size: 14px;
    display: block;
  }

